.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
}

.container {
  width: 400px;
  display: flex;
  position: relative;
  align-items: center;
}

input {
  color: #b3b3b3;
  padding: 5px;
  border: 1px solid #b3b3b3;
  font-size: 20px;
  height: 2.5rem;
  width: 100%;
  padding-left: 20px;
  padding-right: 0px;
  border-radius: 99999px;
  background: transparent;
}

input:focus {
  outline: none;
}

.btn--add {
  position: absolute;
  right: 0;
  background: transparent;
  border: none;
  font-size: 30px;
  display: flex;
  align-items: center;
  color: #b3b3b3;
}

li {
  padding: 20px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
  margin-bottom: 10px;
  background-color: #202020;
}

.content {
  width: 600px;
  margin-bottom: 20px;
}

.btn-item {
  display: flex;
  align-items: center;
}

.btn-item button {
  color: #b3b3b3;
  font-size: 30px;
  display: flex;
  align-items: center;
  border: none;
  background: transparent;
  margin-left: 10px;
}

button {
  cursor: pointer;
}

.remove-all {
  border-radius: 10px;
  font-size: 20px;
  color: #b3b3b3;
  background-color: #202020;
  border: none;
  padding: 10px 20px;
}
